<template>
  <div id="app">
    <keep-alive>
        <router-view/>      
    </keep-alive>
    <tab class="_tab"></tab>
  </div>
</template>

<script>
import tab from '@/base/tab/tab'
export default {
  name: 'App',
  components:{
    tab
  }
}
</script>

<style lang='less'>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>



